<template>
	<YPage :logo="require('@/assets/img/logo_white.png')">
		<Markdown :text="markdownTxt"></Markdown>
	</YPage>
</template>

<script>
	import Markdown from './components/Markdown.vue'
	const files = require.context("@/assets/md",true,/.md$/)
	export default {
		components: {
			Markdown
		},
		data() {
			return {
				markdownTxt:''
				
			}
		},
		mounted() {
			this.getMdFile()
		},
		methods:{
			getMdFile(){
				let query = this.$route.query.name;
				let reg = /(?<=\.)\S+(?=\.md)/g;
				for (let key of files.keys()) {
					if(key.indexOf(query) != -1){
						let match = key.match(reg)
						this.markdownTxt = require('@/assets/md'+ match[0] + '.md')
						break
					}
				}
			}
		}
	}
</script>

