<template>
	<div class="yu-md-wrapper">
		<div class="yu-md-content-wrapper">
			<div class="yu-md-title">目录</div>
			<div class="yu-md-content">
				<div class="item" :class="{'current':current === index}" :key="index" v-for="(item,index) in titles" :style="{ paddingLeft: `${(item.indent + 1) * 15}px` }" @click="handleAnchorClick(item,index)" v-text="item.title"></div>
			</div>
		</div>
		<div class="yu-md-container">
			<VMdPreview :text="text" ref="preview"></VMdPreview>
		</div>
	</div>
</template>

<script>
	import Vue from "vue";
	import VMdPreview from '@kangc/v-md-editor/lib/preview';
	import vuepressTheme from '@kangc/v-md-editor/lib/theme/vuepress.js';
	import '@kangc/v-md-editor/lib/theme/style/vuepress.css';
	import '@kangc/v-md-editor/lib/style/preview.css';
	import Prism from 'prismjs';
	
	VMdPreview.use(vuepressTheme, {
	  Prism,
	  extend(md) {
	    // md为 markdown-it 实例，可以在此处进行修改配置,并使用 plugin 进行语法扩展
	    // md.set(option).use(plugin);
	  },
	});
	Vue.use(VMdPreview);
	export default {
		name: 'Markdown',
		components: {
			VMdPreview
		},
		props: {
			text: {
				type: String,
				default () {
					return ''
				}
			}
		},
		data() {
			return {
				current:0,
				titles:[],
				preview:''
			}
		},
		created() {
			
		},
		mounted() {
			setTimeout(()=>{
				this.init();
			},150)
		},
		methods: {
			init() {
				const anchors = this.$refs.preview.$el.querySelectorAll("h1,h2,h3,h4,h5,h6");
				this.titles = Array.from(anchors).filter(
					(title) => !!title.innerText.trim() //过滤文本内容不为空的要素
				);
			
				if (!this.titles.length) {
					this.titles = [];
					return;
				}
			
				const hTags = Array.from(
					new Set(this.titles.map((title) => title.tagName))
				).sort();
			
				this.titles = this.titles.map((el) => ({
					title: el.innerText,
					lineIndex: el.getAttribute("data-v-md-line"),
					indent: hTags.indexOf(el.tagName),
					el:el
				}));
			},
			handleAnchorClick(anchor,index){
				this.current = index;
				const heading = this.$refs.preview.$el.querySelector(
					`[data-v-md-line="${anchor.lineIndex}"]`
				);
				if (heading) {
					heading.scrollIntoView({
						behavior: "smooth",
						block: 'start'
					});
					//v-md-preview组件自带的滚动方法
					// preview.value.scrollToTarget({
					//   target: heading,
					//   scrollContainer: window,
					//   top: 60,
					// });
				}
			}
		}
	}
</script>
<style lang="scss" scoped>
	@media screen and (min-width: 576px) {
		.yu-md-wrapper {
			width: 1080px;
			margin: 0 auto;
			padding: 10px 0;
			.yu-md-content-wrapper{
				position: fixed;
				top:70px;
				padding-bottom: 10px;
				margin-right: 10px;
				border-radius: 5px;
				background-color: #fff;
				.yu-md-title{
					font-size: 15px;
					color: #666;
					padding: 12px 0;
					margin-left:15px;
					border-bottom: 1px solid #f2f2f2;
				}
				.yu-md-content{
					width: 310px;
					padding-top: 10px;
					padding-bottom: 10px;
					height: 600px;
					overflow: auto;
					.item{
						position: relative;
						font-size: 14px;
						line-height: 24px;
						padding: 8px;
						cursor: pointer;
						color: #666;
						&:hover{
							background-color: #eff3ff;
							font-weight: bolder;
							color: #3565ee;
						}
					}
					.current{
						color: #3565ee;
						font-weight: bolder;
						&::after{
							content: "";
							position: absolute;
							top: 4px;
							left: 0;
							margin-top: 8px;
							width: 4px;
							height: 16px;
							background: #3565ee;
							border-radius: 0 4px 4px 0;    
						}
					}
				}
			}
			.yu-md-container{
				margin-left:320px;
				width: 760px;
				border-radius: 5px;
				overflow: hidden;
				background-color: #fff;
			}
		}
	}
</style>